import "./Row.css";
import axios from "./axios";
import React, { useEffect, useState } from "react";
import Youtube from 'react-youtube';
import movieTrailer from 'movie-trailer';

const baseUrl = "https://image.tmdb.org/t/p/original";

function Row({ title, fatchUrl, isLargeRow }) {
  const [movie, setMovies] = useState([]);
  const [trailerUrl,setTrailerUrl] =useState("")
  useEffect(() => {
    async function fatchdata() {
      const request = await axios.get(fatchUrl);
      setMovies(request.data.results);
      console.log(request.data.results);
      return request;
    }
    fatchdata();
  }, [fatchUrl]);


  const opt = {
    height: "390",
    width: "100%",
    playerVars: {
        autoplay: 1,
    }
   

}

const handleTrailer=()=>{
  if(trailerUrl){
    setTrailerUrl("")
  }
  else{
    movieTrailer(movie?.name || "")
    .then((url)=>{
      const urlParmas= new URLSearchParams(new URL(url).search);
      setTrailerUrl(urlParmas.get("v"));
    })
    .catch(()=>console.log("Temporarily unavailable"))
  }
}
 
 
  return (

    <div className="row">
      {title}
      <div className="row__posters">
        {movie.map((movie) => (
          <img key={movie.id}
          onClick={()=>handleTrailer()}
            className={`row__poster ${isLargeRow && "row__posterLarge"}`}
            src={`${baseUrl}${
              isLargeRow ? movie.poster_path : movie.backdrop_path
            }`}
            alt={movie.name}
          />
        ))}
      </div>
      {trailerUrl && <Youtube videoId={trailerUrl} opts={opt}></Youtube>}
    </div>
               

  );
}


export default Row

