import React,{useState,useEffect} from "react";
import "./Banner.css";
import axios from './axios';
import request from "./request";


 


function Banner() {



const [movies,setMovies] = useState([])
useEffect(()=>{
  
async function fetchdata(){
  const req = await axios.get(request.fetchNetFlixOriginals)
  setMovies(req.data.results[
    Math.floor(Math.random()*req.data.results.length)
  ])
  console.log ( req.data.results)
} 
  fetchdata();
},[])
 
function trucateDesc(str,n){
  return str?.length > n ? str.substr(0,n-1) +"....":str
}
  return (

    <header
      className="banner"
      style={{
        backgroundSize: "cover",
        backgroundRepeat: "none",
        backgroundImage: `url("https://image.tmdb.org/t/p/original${movies?.backdrop_path}")`,
        backgroundPosition: "center center",
      }}
    >
      <div className="banner__contents">
        <h1 className="banner__title">{movies?.title || movies?.name || movies?.original_name}</h1>
        <div className="banner__buttons">
          <button className="banner__button">play</button>
          <button className="banner__button">my List</button>
        </div>
        <h1 className="banner__description">{trucateDesc(movies?.overview,100)}</h1>
      </div>
      <div className="banner__fadeBottom">

      </div>
    </header>
  );
}

export default Banner;
